<h1 id="welcome" tabindex="-1"><a class="header-anchor" href="#welcome" aria-hidden="true">#</a> Welcome!</h1>
<p>Welcome to the course <em>Web Development Fundamentals</em>! In this course you'll learn how to create websites consisting of webpages generated by web applications running on the Internet and that are part of the web. Users can then visit your websites using a web browser.</p>
<div class="custom-container warning"><p class="custom-container-title">Note to old students</p>
<p>If you're an old student that took this course a previous year but still have some examination tests left to pass you take those tests as they are described this year. The old Canvas Course won't be used any more, so join this year's Canvas Course, so we can keep track of your progress in the course this year there. If you aren't registered for the course this year, ask the course coordinator to invite you to the Canvas Course by sending an email message with the topic <code>Invitation to Canvas Course</code> to <code>peter.larsson-green@ju.se</code> with the content based on the template in <FigureNumber /> below.</p>
